<template>
  <card title="Login" :sub-title="`${message}`">
    <form @submit.prevent>
      <div class="row">
        <div class="col-md-5">
          <fg-input
            type="text"
            id="email"
            v-model="email"
            required
            placeholder=" ..."
            v-bind:class="{
              error: error.email,
              complete: !error.email && email.length !== 0
            }"
          ></fg-input>
          <div class="error-text" v-if="error.email">{{ error.email }}</div>
          <!-- v-bind, div error text -->
        </div>
      </div>

      <div class="row">
        <div class="col-md-5">
          <fg-input
            type="password"
            id="password"
            v-model="password"
            required
            placeholder=" ..."
            @keypress.enter="login"
            v-bind:class="{
              error: error.password,
              complete: !error.password && password.length !== 0
            }"
          ></fg-input>
          <div class="error-text" v-if="error.password">
            {{ error.password }}
          </div>
          <!-- v-bind, div error text -->
        </div>
      </div>
      <div class="row">
        <div class="col-md-5">
          <b-button type="button" variant="link" class="m-1" @click="findpw">
             ?</b-button
          >
        </div>
      </div>
    </form>
    <b-button type="button" variant="dark" class="m-1" @click="login"
      ></b-button
    >
    <b-button type="button" variant="dark" class="m-1" @click="regist"
      ></b-button
    >
  </card>
</template>

<script>
import Vue from "vue";
import VueRouter from "vue-router";
import Card from "@/components/Cards/Card.vue";

//    npm  import
import PV from "password-validator";
import * as EmailValidator from "email-validator";

Vue.use(VueRouter);

export default {
  components: { Card },
  name: "Login",
  created() {
    this.passwordSchema
      //   2
      .is()
      .min(2)
      //   100
      .is()
      .max(100)
      // 1  
      .has()
      .digits(1)
      //  
      .has()
      .not()
      .spaces();
    //   
    // .has().uppercase()
    //   
    // .has().lowercase()
    //     -  
    // .is().not().oneOf(['', '']);
  },
  data: function() {
    return {
      email: "",
      password: "",
      message: "",

      //  , error 
      passwordSchema: new PV(),
      error: {
        email: false,
        passowrd: false
      },
      isSubmit: false,
      component: this
    };
  },
  // computed: {
  //   nextRoute() {
  //     return this.$route.params.nextRoute ? this.$route.params.nextRoute : '';
  //   },
  // },

  //    watch :   
  watch: {
    password: function(v) {
      this.checkForm();
    },
    email: function(v) {
      this.checkForm();
    }
  },

  methods: {
    // checkForm() 
    checkForm() {
      if (this.email.length >= 0 && !EmailValidator.validate(this.email))
        this.error.email = "  .";
      else this.error.email = false;

      if (
        this.password.length >= 0 &&
        !this.passwordSchema.validate(this.password)
      )
        this.error.password = ",   8  .";
      else this.error.password = false;

      let isSubmit = true;
      Object.values(this.error).map(v => {
        if (v) isSubmit = false;
      });
      this.isSubmit = isSubmit;
    },
    //   
    regist: function() {
      this.$router.replace("/user/register");
    },
    login: function() {
      //   let  : dispatch  this.user-> user
      let {email, password} = this;
      let user = { email, password};

      // LOGIN  
      //  (axios)     Actions .
      this.$store
        .dispatch("LOGIN", user)
        .then(() => this.$router.push(`/dashboard`))
        // .then(() => this.$router.replace(`/${this.nextRoute}`))
        .catch(
          ({ message }) => (this.message = "/ .")
        );
    },
    findpw: function() {
      this.$router.replace("/user/findpw");
    }
  },
  // created() {
  //   this.$store
  //     .dispatch("LOGOUT")
  //     .then(() => this.$router.replace("/login").catch(() => {}));
  // }
};
</script>

<style scope>
#login-div {
  text-align: center;
}
</style>
