<template>
  <table class="table table-bordered w-20">
    <tr>
      <th></th>
      <td>
        <input
          type="text"
          class="form-control"
          id="name"
          ref="name"
          placeholder=" "
          v-model="name"
        />
      </td>
    </tr>
    <tr>
      <th></th>
      <td>
        <textarea
          type="text"
          class="form-control"
          id="text"
          ref="text"
          placeholder=" "
          v-model="text"
        ></textarea>
      </td>
    </tr>
    <tr>
      <td colspan="2">
        <button class="btn btn-primary" @click="checkHandler">
          
        </button>
      </td>
    </tr>
  </table>
</template>
<script>
const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: "board-Form",
  props: {
    type: { type: String },
    noq: { type: Number }
  },
  data: function() {
    return {
      noa: "",
      userid: "",
      name: "",
      text: "",
      regDate: ""
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.name &&
        ((msg = " "), (err = false), this.$refs.name.focus());
      err &&
        !this.text &&
        ((msg = " "), (err = false), this.$refs.text.focus());
      if (!err) alert(msg);
      //else this.type == 'create' ? this.createHandler() : this.updateHandler();
      else this.createHandler();
    },

    createHandler() {
      axios
        .post(`${SERVER_URL}qna/a`, {
          noq: this.noq,
          userid: this.name,
          name: this.name,
          title: this.title,
          text: this.text,
          regDate: this.regDate
        })
        .then(({ data }) => {
          let msg = "   .";
          if (data == 0) {
            alert(msg);
          }
          this.$emit("update");
          this.name = "";
          this.text = "";
        })
        .catch(() => {
          alert("   .");
        });
    }
  }
};
</script>
