<template>
  <card title="">
    <div class="form-group">
      <label for="name"></label> &nbsp;
      <input
        type="text"
        class="form-control"
        id="name"
        ref="name"
        placeholder=" "
        v-model="name"
      />
    </div>
    <br />
    <div class="form-group">
      <label for="title"></label> &nbsp;
      <input
        type="text"
        class="form-control"
        id="title"
        ref="title"
        placeholder=" "
        v-model="title"
      />
    </div>
    <br />
    <div class="form-group">
      <label for="text"></label> &nbsp;
      <textarea
        type="text"
        class="form-control"
        id="text"
        ref="text"
        placeholder=" "
        v-model="text"
      ></textarea>
    </div>
    <br />
    <div class="text-right">
      <button
        class="btn btn-primary"
        v-if="type != 'QuestionForm'"
        @click="checkHandler"
      >
        
      </button>
      <button class="btn btn-primary" v-else @click="checkHandler"></button>
      &nbsp;
      <button class="btn btn-primary" @click="moveList"></button>
    </div>
    <br />
  </card>
</template>

<script>
const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: "board-Form",
  props: {
    type: { type: String }
  },
  data: function() {
    return {
      noq: "",
      userid: "",
      name: "",
      title: "",
      text: "",
      regDate: ""
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.name &&
        ((msg = " "), (err = false), this.$refs.name.focus());
      err &&
        !this.title &&
        ((msg = " "),
        (err = false),
        this.$refs.title.focus());
      err &&
        !this.text &&
        ((msg = " "), (err = false), this.$refs.text.focus());
      if (!err) alert(msg);
      //else this.type == 'create' ? this.createHandler() : this.updateHandler();
      else this.createHandler();
    },

    createHandler() {
      axios
        .post(`${SERVER_URL}qna/q`, {
          noq: this.noq,
          userid: this.userid,
          name: this.name,
          title: this.title,
          text: this.text,
          regDate: this.regDate
        })
        .then(({ data }) => {
          let msg = "   .";
          if (data > 0) {
            msg = " .";
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert("   .");
        });
    },
    moveList() {
      this.$router.push("/qna/list");
    }
  }
};
</script>

<style scoped></style>
