<template>
  <div>...</div>
</template>

<script>
const SERVER_URL = process.env.VUE_APP_SERVER_URL;
export default {
  name: "delete",
  created() {
    axios
      .delete(`${SERVER_URL}qna/q/${this.$route.query.noq}`)
      .then(({ data }) => {
        let msg = "   .";
        if (data > 0) {
          msg = " .";
        }
        alert(msg);
        this.$router.push("/qna/list");
      })
      .catch(() => {
        alert("   .");
      });
  }
};
</script>

