<template>
  <card title="">
    <div class="form-group">
      <label for="title"></label> &nbsp;
      <input
        type="text"
        class="form-control"
        id="title"
        ref="title"
        placeholder=" "
        v-model="title"
      />
    </div>
    <br />

    <div class="form-group">
      <label for="writer"></label> &nbsp;
      <input
        type="text"
        class="form-control"
        id="writer"
        ref="writer"
        placeholder=" "
        v-model="writer"
      />
    </div>
    <br />

    <div class="form-group">
      <label for="context"></label> &nbsp;
      <textarea
        type="context"
        class="form-control"
        id="context"
        ref="context"
        placeholder=" "
        v-model="context"
      ></textarea>
    </div>
    <br />


    <div class="text-right">
      <button
        class="btn btn-dark"
        @click="checkHandler"
      >
        
      </button>

      <button class="btn btn-dark" @click="moveList"></button>
    </div>
    <br />
  </card>
</template>

<script>
const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: "board-Form",
  props: {
    type: { type: String }
  },
  data: function() {
    return {
      no: "",
      title: "",
      writer: "",
      date: "",
      context: "",
      click: "",
      flag:""
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.writer &&
        ((msg = " "), (err = false), this.$refs.writer.focus());
      err &&
        !this.title &&
        ((msg = " "),
        (err = false),
        this.$refs.title.focus());
      err &&
        !this.context &&
        ((msg = " "), (err = false), this.$refs.context.focus());
      if (!err) alert(msg);
      //else this.type == 'create' ? this.createHandler() : this.updateHandler();
      else this.createHandler();
    },

    createHandler() {
      axios
        .post(`${SERVER_URL}/board/insert`, {
        //   noq: this.noq,
        //   userid: this.userid,
        //   name: this.name,
        //   title: this.title,
        //   text: this.text,
        //   regDate: this.regDate
        })
        .then(({ data }) => {
          let msg = "   .";
          if (data > 0) {
            msg = " .";
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert("   .");
        });
    },
    moveList() {
      this.$router.push("/boardlist");
    }
  }
};
</script>

<style scoped></style>
