<template>
  <div>
    <table class="table table-bordered w-100">
      <!-- <table class="table table-bordered w-20"> -->
      <tr>
        <th></th>
        <td>{{ name }}</td>
        <th></th>
        <td>{{ getFormatDate(regDate) }}</td>
      </tr>
      <tr>
        <td colspan="4">
          {{ text }}
        </td>
      </tr>
      <tr>
        <td colspan="4">
          <b-button variant="danger" @click="deleteHandler"></b-button>
        </td>
      </tr>
    </table>

    <br />
  </div>
</template>

<script>
const SERVER_URL = process.env.VUE_APP_SERVER_URL;
import moment from "moment";
export default {
  name: "AnswerDetail",
  props: {
    noa: { type: Number },
    noq: { type: Number },
    name: { type: String },
    text: { type: String },
    regDate: { type: String }
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD");
    },

    deleteHandler() {
      axios
        .delete(`${SERVER_URL}qna/a/${this.noa}`, {
          noq: this.noq,
          userid: this.name,
          name: this.name,
          title: this.title,
          text: this.text,
          regDate: this.regDate
        })
        .then(({ data }) => {
          let msg = "   .";
          if (data == 0) {
            alert(msg);
          }
          this.$emit("delete");
        })
        .catch(() => {
          alert("   .");
        });
    }
  }
};
</script>
<style>
th {
  width: 10%;
  text-align: center;
}
</style>
