<template>
  <card title="check" :sub-title="`${message}`">
    <div class="row">
      <div class="col-md-5">
        <fg-input
          type="text"
          label="email"
          :readonly="true"
          v-model="user.email"
        >
        </fg-input>
      </div>
    </div>

    <div class="row">
      <div class="col-md-6">
        <fg-input
          type="password"
          label="Password Confirm"
          placeholder="  "
          v-model="user.password"
        >
        </fg-input>
      </div>
    </div>

    <div class="row">
      <div class="col-md-6">
        <b-button variant="primary" @click="check" class="col-md-6"
          ></b-button
        >
      </div>
    </div>
  </card>
</template>

<script>
import axios from "axios";

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  data() {
    return {
      user: {
        email: ""
      },
      message: ""
    };
  },
  methods: {
    check: function() {
      // LOGIN  
      //  (axios)     Actions .
      this.$store
        .dispatch("LOGIN", this.user)
        .then(() => this.$router.push(`/user/update`))
        // .then(() => this.$router.replace(`/${this.nextRoute}`))
        .catch(({ message }) => (this.message = " ."));
    }
  },
  created() {
    this.user.email = this.$store.getters["getEmail"];
  }
};
</script>

<style></style>
