<template>
  <card title="findpw" :sub-title="`${message}`">
    <form @submit.prevent>
      <div class="row">
        <div class="col-md-5">
          <fg-input
            type="text"
            id="email"
            v-model="user.email"
            required
            placeholder=" ...."
          ></fg-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-5">
          <fg-input
            type="text"
            id="name"
            v-model="user.name"
            required
            placeholder=" ...."
            @keypress.enter="findpw"
          ></fg-input>
        </div>
      </div>
    </form>
    <b-button type="button" variant="dark" class="m-1" @click="findpw"
      > </b-button
    >
    <b-button type="button" variant="dark" class="m-1" @click="cancel"
      ></b-button
    >
  </card>
</template>

<script>
import Vue from "vue";
import VueRouter from "vue-router";
import Card from "@/components/Cards/Card.vue";

Vue.use(VueRouter);

export default {
  components: { Card },
  name: "findpw",
  data: function() {
    return {
      user: {
        email: "",
        name: ""
      },
      message: ""
    };
  },
  // computed: {
  //   nextRoute() {
  //     return this.$route.params.nextRoute ? this.$route.params.nextRoute : '';
  //   },
  // },
  methods: {
    cancel: function() {
      this.$router.replace("/login");
    },
    findpw: function() {
      // LOGIN  
      //  (axios)     Actions .
      this.$store
        .dispatch("FINDPW", this.user)
        .then(() => this.$router.push('/user/findpwsuccess'))
        //.then(({ message }) => (this.message = "  "))
        // .then(() => this.$router.replace(/${this.nextRoute}))
        .catch(
          ({ message }) => (this.message = "  .")
        );
    },
  }
};
</script>
